@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
.alert {
  bottom: 0;
  left: 50%;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 24px;
  z-index: 1000;
  position: fixed;
  font-size: 14px;
  min-width: 200px;
  border-radius: 24px;
  background: var(--white);
  transition: all 0.3s ease;
  padding: 10px 12px 10px 20px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.alert.alert--active {
  transform: translate(calc(-50% - 24px), -80%);
}

.alert.alert--inactive {
  transform: translate(calc(-50% - 24px), 150%);
}

.alert__message {
  color: var(--text-color-primary);
}

.alert__icon {
  width: 24px;
  height: 24px;
  padding: 8px;
  display: flex;
  cursor: pointer;
  margin-left: 16px;
  border-radius: 24px;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease;
}
.alert__icon:hover {
  background: var(--primary-bg);
}

.close__icon {
  width: 16px;
  height: 16px;
}

.users__wrapper {
  padding: 24px;
  border-radius: 24px;
  background: var(--white);
  height: calc(100% - 48px);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.users__heading {
  font-size: 24px;
  font-weight: 500;
  color: var(--text-color-primary);
}

.user__wrapper {
  padding-top: 12px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.user__name {
  font-size: 16px;
  padding: 0px 12px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: calc(100% - 68px);
  color: var(--text-color-primary);
}

.avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
  font-size: 16px;
  font-weight: 500;
  border-radius: 50%;
  background: var(--white);
  color: var(--text-color-primary);
}

.avatar.avatar--bg {
  background: var(--primary-bg);
}

.button {
  border: none;
  outline: none;
  min-width: 112px;
  cursor: pointer;
  color: #ffffff;
  font-size: 16px;
  min-height: 48px;
  padding: 12px 16px;
  border-radius: 24px;
  background: var(--primary);
  transition: box-shadow 0.3s ease;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.button.button--block {
  width: 100%;
}

.button:hover {
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.message__wrapper {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
}
.message__wrapper--right {
  justify-content: flex-end;
}

.message__container {
  max-width: 50%;
  margin-top: 24px;
  font-size: 14px;
  padding: 12px 20px;
  background: var(--white);
  color: var(--text-color-primary);
  border-radius: 24px 24px 24px 0px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.message__container--right {
  color: #ffffff;
  text-align: right;
  background: var(--primary);
  border-radius: 24px 24px 0px 24px;
}

.message__bot {
  width: 100%;
  font-size: 16px;
  padding-top: 24px;
  text-align: center;
  color: var(--text-color-secondary);
}

.message__text {
  font-size: 16px;
  word-break: break-all;
}

.message__timestamp {
  font-size: 10px;
  color: var(--text-color-secondary);
}

.message__timestamp--right {
  text-align: right;
  color: #ffffff;
  opacity: 0.6;
}

.input__wrapper {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  min-height: 36px;
  border-radius: 24px;
  background: var(--white);
  padding: 8px 14px 8px 18px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.input__send {
  width: 24px;
  height: 24px;
  display: flex;
  cursor: pointer;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  padding: 10px 11px 10px 9px;
  transition: background 0.3s ease;
}
.input__send:hover {
  background: var(--primary-bg);
}

.send_icon {
  width: 24px;
  height: 24px;
}

.input {
  width: 100%;
  border: none;
  outline: none;
  font-size: 16px;
  background: transparent;
  color: var(--text-color-primary);
}
.input--shrunk {
  width: calc(100% - 42px);
}

.messages__wrapper {
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.messages__container {
  display: flex;
  overflow-y: auto;
  padding-bottom: 24px;
  flex-direction: column;
  max-height: calc(100% - 60px - 24px);
}

.chat__wrapper {
  background: var(--primary);
  height: calc(100vh - 48px);
  padding: 24px;
}

.chat__container {
  margin: 0 auto;
  padding-top: 24px;
  max-width: 1000px;
  border-radius: 24px;
  height: calc(100% - 48px);
  background: var(--primary-bg);
}

.chat__header {
  margin: 0px 24px;
  display: flex;
  padding: 12px 24px;
  border-radius: 24px;
  align-items: center;
  background: var(--white);
  justify-content: space-between;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.chat__name {
  font-size: 28px;
  font-weight: 600;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: var(--text-color-primary);
}

.chat__actions {
  display: flex;
  align-items: center;
  justify-content: center;
}
.chat__theme {
  width: 24px;
  height: 24px;
  display: flex;
  cursor: pointer;
  border-radius: 50%;
  margin-right: 12px;
  align-items: center;
  justify-content: center;
  padding: 10px 11px 10px 9px;
  transition: background 0.3s ease;
}
.chat__theme:hover {
  background: var(--primary-bg);
}

.chat__body {
  display: flex;
  align-items: flex-start;
  width: calc(100% + 24px);
  height: calc(100% - 48px - 24px);
  justify-content: flex-start;
}

.chat__users,
.chat__messages {
  padding: 24px;
  overflow-y: auto;
  border-radius: 24px;
  height: calc(100% - 48px);
}

.chat__users {
  width: 280px;
}

.chat__messages {
  right: 24px;
  position: relative;
  width: calc(100% - 280px);
}

@media (max-width: 640px) {
  .chat__users {
    width: 0px;
    padding: 0px;
    visibility: hidden;
  }
  .chat__body {
    width: 100%;
  }
  .chat__messages {
    width: 100%;
    position: static;
  }
}

.join__wrapper {
  height: 100%;
  margin: 0 auto;
  display: flex;
  padding: 0px 24px;
  align-items: center;
  justify-content: center;
  background: var(--primary);
}

.join__container {
  width: 300px;
  padding: 28px 24px;
  border-radius: 32px;
  background: var(--primary-bg);
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', 'Courier New', Courier, monospace;
  -webkit-tap-highlight-color: transparent;
}

.app {
  background: var(--primary-bg);
  height: 100vh;
  width: auto;
}

::-webkit-input-placeholder {
  color: var(--text-color-disabled);
}

:-ms-input-placeholder {
  color: var(--text-color-disabled);
}

::placeholder {
  color: var(--text-color-disabled);
}

